import {applyToPoints} from 'transformation-matrix/applyToPoint'
import {centerOf} from './centerOf'

export function applyTransform(before: Outline, transform: Matrix): Outline {
    const {x, y, width, height} = before

    const center = centerOf(before)
    const beforeTopLeft = {x: x - center.x, y: y - center.y}
    const beforeBottomRight = {x: x + width - center.x, y: y + height - center.y}
    const [afterTopLeft, afterBottomRight] =
        applyToPoints(transform, [beforeTopLeft, beforeBottomRight])

    const after: Outline = {
        x: afterTopLeft.x + center.x,
        y: afterTopLeft.y + center.y,
        width: afterBottomRight.x - afterTopLeft.x,
        height: afterBottomRight.y - afterTopLeft.y,
        opacity: before.opacity
    }
    return after
}
