import {hash, is, List, Map} from 'immutable'
import {existing, notExisting} from '../../../../typecheck/existing'
import {isNonpositiveInteger} from '../../../../typecheck/isInteger'
import {add} from '../../../../utils/add'
import {assume} from '../../../../utils/assume'
import {ensure} from '../../../../utils/ensure'
import {greater} from '../../../../utils/greater'
import {ImmutableList} from '../../../../utils/ImmutableList'
import {ImmutableMap} from '../../../../utils/ImmutableMap'
import {lesser} from '../../../../utils/lesser'
import {nothing} from '../../../../utils/nothing'
import {byIndex} from './byIndex'
import {ListItemID, ListItemPosition, ListLayout} from './ListLayout'
import {ListLayoutImpl} from './ListLayoutImpl';
import {Cursor, MasonryItemPosition, MasonryLayout} from './MasonryLayout'

export class MasonryLayoutImpl implements MasonryLayout {
    public get itemIDs(): ImmutableList<ListItemID> { return this.indexToID }

    public get size(): number { return this.indexToID.size }

    public get columns(): number { return this.listLayouts.length }

    public get totalHeight(): px {
        const {defaultHeight, listLayouts, cursor, columns, size} = this
        const highest = listLayouts.map(it => it.totalHeight).reduce(greater)
        const itemsLeft = size - 1 - cursor.index
        const heightLeft = Math.ceil(itemsLeft / columns) * defaultHeight
        return highest + heightLeft
    }

    public get updatedHeights(): ImmutableMap<ListItemID, px> { return this.knownHeights }

    public get cursor(): Cursor {
        const {indexToID, listLayouts} = this
        const index = listLayouts.map(it => it.size).reduce(add, -1)
        const offset = listLayouts.map(it => it.totalHeight).reduce(lesser)
        const id = assume(index, index => indexToID.get(index))

        return {index, offset, id}
    }

    public getByID(id: ListItemID): Maybe<MasonryItemPosition> {
        const {listLayouts, idToIndex} = this

        const index = idToIndex.get(id)
        if (notExisting(index))
            return nothing

        const position = listLayouts
            .map((it, column) => ({column, item: it.getByID(id)}))
            .filter(({column, item}) => existing(item))
            .pop()
        if (notExisting(position) || notExisting(position.item))
            return nothing
        return toMasonryItemPosition(position.item, position.column, index)
    }

    public getByIndex(index: number): Maybe<MasonryItemPosition> {
        const {indexToID} = this
        if (!isNonpositiveInteger(index))
            throw new Error(`${index} is not a legal index`)

        const id = indexToID.get(index)
        return assume(id, id => this.getByID(id))
    }

    public getByIndexRange(from: number, to: number): Array<MasonryItemPosition> {
        const {cursor} = this
        from = Math.min(Math.max(from, 0), cursor.index)
        to = Math.min(Math.max(to, 0), cursor.index)

        const results: Array<MasonryItemPosition> = []
        for (let i = from; i <= to; i++) {
            const item = this.getByIndex(i)
            if (notExisting(item))
                break
            results.push(item)
        }
        return results
    }

    public getByOffset(position: px): Array<MasonryItemPosition> {
        const {listLayouts, idToIndex} = this

        return listLayouts
            .map((listLayout, column) => {
                const listPosition = listLayout.getByOffset(position)
                const index = assume(listPosition, it => idToIndex.get(it.id))
                if (notExisting(listPosition) || notExisting(index))
                    return nothing
                return toMasonryItemPosition(listPosition, column, index)
            })
            .filter(existing)
            .sort(byIndex)
    }

    public getByOffsetRange(from: px, to: px): Array<MasonryItemPosition> {
        const {listLayouts, idToIndex, cursor} = this
        from = Math.min(Math.max(from, 0), cursor.offset)
        to = Math.min(Math.max(to, 0), cursor.offset)

        return listLayouts
            .map((listLayout, column) => {
                const fromItem = listLayout.getByOffset(from)
                const toItem = listLayout.getByOffset(to)
                if (notExisting(fromItem) || notExisting(toItem))
                    return []
                const items = listLayout.getByIndexRange(fromItem.index, toItem.index)
                return items.map(item =>
                    toMasonryItemPosition(item, column, idToIndex.get(item.id)!)
                )
            })
            .reduce((a, b) => a.concat(b))
            .sort(byIndex)
    }

    public equals(other: MasonryLayout): boolean {
        return other.hashCode() === this.hashCode()
    }

    public hashCode(): number {
        const {listLayouts} = this
        return hash(listLayouts.map(it => it.hashCode()).join('-'));
    }

    public append(additionalItemIDs: ImmutableList<ListItemID>): this | MasonryLayoutImpl {
        const {indexToID, idToIndex, cursor} = this

        if (additionalItemIDs.size === 0)
            return this

        if (additionalItemIDs.some(id => idToIndex.has(id)))
            throw new Error('adding existing id')

        const originSize = indexToID.size
        const newIndexToID = indexToID.concat(additionalItemIDs)
        const newIDToIndex = idToIndex.withMutations(idToIndex => {
            additionalItemIDs.forEach((id, indexDiff) => {
                idToIndex.set(id, indexDiff + originSize)
            })
        })

        return this.change({newIDToIndex, newIndexToID}).reconcile({index: cursor.index})
    }

    public update(newShapes: ImmutableMap<ListItemID, px>): this | MasonryLayoutImpl {
        const {listLayouts, knownHeights, cursor} = this

        const emptyUpdate = newShapes.size === 0
        const noNewHeight = newShapes.every((height, id) => knownHeights.get(id) === height)
        if (emptyUpdate || noNewHeight) {
            return this
        }

        const newKnownHeights = knownHeights.withMutations(knownHeights => {
            newShapes.forEach((height, id) => {
                knownHeights.set(id, height)
            })
        })

        const newListLayouts = listLayouts.map(listLayout => listLayout.update(newShapes))

        return this.change({newKnownHeights, newListLayouts}).reconcile({index: cursor.index})
    }

    public reconcile(upTo: Partial<Cursor>): this | MasonryLayoutImpl {
        let target: MasonryLayoutImpl = this

        if (existing(upTo.index))
            target = target.updateToIndex(upTo.index)
        if (existing(upTo.id))
            target = target.updateToID(upTo.id)
        if (existing(upTo.offset))
            target = target.updateToOffset(upTo.offset)

        return target
    }

    public replace(newIndexToID: ImmutableList<ListItemID>): this | MasonryLayoutImpl {
        const {indexToID, defaultHeight, knownHeights, columns, cursor} = this

        const sameIDs = is(newIndexToID, indexToID)
        if (sameIDs)
            return this

        const newIDToIndex = Map<ListItemID, number>().withMutations(newIDToIndex => {
            newIndexToID.forEach((id, index) =>
                newIDToIndex.set(id, index)
            )
        })

        const newListLayouts = new Array(columns).map(() =>
            ListLayoutImpl.create([], defaultHeight, knownHeights)
        )

        return this.change({newIDToIndex, newListLayouts}).reconcile({offset: cursor.offset})
    }

    public reflow(columns: px): MasonryLayoutImpl {
        const {defaultHeight, knownHeights, cursor} = this

        columns = Math.max(columns, 1)
        const newListLayouts = new Array(columns).fill(nothing).map(() =>
            ListLayoutImpl.create([], defaultHeight, knownHeights)
        )

        return this.change({newListLayouts}).reconcile({index: cursor.index})
    }

    public clone(): MasonryLayoutImpl {
        const {
            defaultHeight, indexToID, idToIndex,
            knownHeights, listLayouts
        } = this

        const newListLayouts = listLayouts.map(it => it.clone())

        return new MasonryLayoutImpl(
            defaultHeight,
            indexToID,
            idToIndex,
            knownHeights,
            newListLayouts
        )
    }

    public static create(itemIDs: ImmutableList<ListItemID>,
                         defaultHeight: px,
                         columns: number,
                         knownHeights?: Map<ListItemID, px>): MasonryLayout {
        const indexToID = List(itemIDs)
        const idToIndex = Map<ListItemID, number>().withMutations(idToIndex =>
            indexToID.forEach((id, index) =>
                idToIndex.set(id, index)
            )
        )
        columns = Math.max(columns, 1)
        knownHeights = ensure(knownHeights, Map())
        const listLayouts = new Array(columns).fill(nothing).map(() =>
            ListLayoutImpl.create([], defaultHeight, knownHeights)
        )

        return new MasonryLayoutImpl(
            defaultHeight,
            indexToID,
            idToIndex,
            knownHeights,
            listLayouts
        )
    }

    private constructor(
        private readonly defaultHeight: px,
        private readonly indexToID: List<ListItemID>,
        private readonly idToIndex: Map<ListItemID, number>,
        private readonly knownHeights: Map<ListItemID, px>,
        private readonly listLayouts: ReadonlyArray<ListLayout>
    ) {}

    private updateToIndex(index: number): MasonryLayoutImpl {
        const {
            defaultHeight, knownHeights, listLayouts,
            indexToID, cursor
        } = this

        if (cursor.index >= index)
            return this
        // console.log('cursor', cursor)

        index = Math.min(Math.max(index, 0), indexToID.size - 1)
        const listHeights = listLayouts.map(it => it.totalHeight)
        const ids = listLayouts.map(() => [] as Array<ListItemID>)
        const updates = listLayouts.map(() => Map<ListItemID, px>())
        for (let pos = cursor.index + 1; pos <= index; pos++) {
            const column = listHeights
                .map((height, column) => ({height, column}))
                .reduce((prev, curr) => prev.height <= curr.height ? prev : curr)
                .column
            const id = indexToID.get(pos)
            if (notExisting(id))
                throw new Error(`unexpected index ${pos}`)
            ids[column].push(id)
            const height = knownHeights.get(id)
            if (existing(height))
                updates[column].set(id, height)
            listHeights[column] += ensure(height, defaultHeight)
        }

        const newListLayouts = listLayouts.map((layout, column) =>
            layout.append(ImmutableList(ids[column])).update(ImmutableMap(updates[column]))
        )

        return this.change({newListLayouts})
    }

    private updateToID(id: ListItemID): MasonryLayoutImpl {
        const {idToIndex} = this
        const index = idToIndex.get(id)
        if (notExisting(index))
            return this
        return this.updateToIndex(index)
    }

    private updateToOffset(offset: px): MasonryLayoutImpl {
        const {defaultHeight, listLayouts, columns, cursor} = this
        if (cursor.offset >= offset)
            return this
        const shortest = listLayouts.map(it => it.totalHeight).reduce(lesser)
        const itemsFill = Math.ceil((offset - shortest) / defaultHeight) * columns
        if (itemsFill <= 0)
            return this
        return this.updateToIndex(cursor.index + Math.ceil(itemsFill))
    }

    private change(params: Partial<{
        newDefaultHeight: px,
        newIndexToID: ImmutableList<ListItemID>,
        newIDToIndex: ImmutableMap<ListItemID, number>,
        newKnownHeights: ImmutableMap<ListItemID, px>,
        newListLayouts: ReadonlyArray<ListLayout>
    }>): MasonryLayoutImpl {
        const {defaultHeight, indexToID, idToIndex, knownHeights, listLayouts, cursor} = this
        const {newDefaultHeight, newIndexToID, newIDToIndex, newKnownHeights, newListLayouts} = params
        return new MasonryLayoutImpl(
            ensure(newDefaultHeight, defaultHeight),
            ensure(newIndexToID, indexToID),
            ensure(newIDToIndex, idToIndex),
            ensure(newKnownHeights, knownHeights),
            ensure(newListLayouts, listLayouts)
        ).reconcile({index: cursor.index})
    }
}

function toMasonryItemPosition(listItemPosition: ListItemPosition,
                               column: number,
                               index: number): MasonryItemPosition {
    return {...listItemPosition, column, index, indexInColumn: listItemPosition.index}
}
