export function isSimilarOutline(a: Outline, b: Outline): boolean {
    const near = (a: number, b: number) => Math.abs(a - b) <= 5
    const isSimilar = near(a.x, b.x) &&
        near(a.y, b.y) &&
        near(a.width, b.width) &&
        near(a.height, b.height) &&
        near(a.opacity * 100, b.opacity * 100)
    return isSimilar
}
