import {inverse} from 'transformation-matrix/inverse'
import {applyTransform} from './applyTransform'
import {calcOutline} from './calcOutline'
import {getTransform} from './getTransform'

// ignoring rotate
export function calcOutlineOrigin(dom: Element): Outline {
    let outline: Outline = calcOutline(dom)

    const transform = getTransform(dom)
    outline = applyTransform(outline, inverse(transform))

    outline.opacity = 1

    return outline
}
