import {Observable} from 'rxjs/Observable'
import {debounceTime, distinctUntilChanged, map, merge, timestamp} from 'rxjs/operators'
import {Pipe} from './Pipe'

export function auditTimeStable<T>(time: ms): Pipe<T> {
    return function (input: Observable<T>): Observable<T> {
        return input.pipe(
            timestamp(),
            distinctUntilChanged((prev, curr) => curr.timestamp - prev.timestamp < time),
            map(t => t.value),
            merge(input.pipe(debounceTime(time)))
        )
    }
}
