import {fromString} from 'transformation-matrix/fromString'
import {identity} from 'transformation-matrix/identity'
import {hasStyle} from './hasStyle'

export function getTransform(element: Element): Matrix {
    const transformCSS = getComputedStyle(element).transform
    const transform = hasStyle(transformCSS) ? fromString(transformCSS) : identity()
    return transform
}
