import {scale} from 'transformation-matrix/scale'
import {transform} from 'transformation-matrix/transform'
import {translate} from 'transformation-matrix/translate'
import {centerOf} from './centerOf'

export function intermediateTransform(from: Outline, to: Outline): Matrix {
    const fromMiddle = centerOf(from)
    const toMiddle = centerOf(to)

    const matrix = transform(
        translate(toMiddle.x - fromMiddle.x, toMiddle.y - fromMiddle.y),
        scale(to.width / from.width, to.height / from.height)
    )
    // console.log(from, to, matrix)
    // console.log(from, matrix, applyTransform(from ,matrix), to)

    return matrix
}

