import {assume} from '../../../utils/assume'
import {ensure} from '../../../utils/ensure'
import {MasonryItemPosition, MasonryLayout} from './layouts/MasonryLayout'

export function projectScroll(scroll: px,
                              height: px,
                              focusOffset: px,
                              from: MasonryLayout,
                              to: MasonryLayout): px {
    // console.log('from columns', from.columns)
    // console.log('to columns', to.columns)
    const focusRatio = focusOffset / height
    const focusRatioDiff = (item: MasonryItemPosition) => {
        const ratio = (focusOffset - (item.offset - scroll)) / item.height
        const diff = ratio - focusRatio
        // console.log('id', item.id, 'diff', diff)
        return diff
    }
    const candidates = from.getByOffset(scroll + focusOffset)
    // console.log('candidates', candidates.map(it=> it.id))
    const anchor = candidates
        .sort((a, b) => Math.abs(focusRatioDiff(a)) - Math.abs(focusRatioDiff(b)))
        [0]
    // console.log('anchor', anchor)
    const anchorCurr = assume(anchor, ({id}) => to.getByID(id))
    // console.log('anchorCurr', anchorCurr)
    const newScroll = assume(anchor, anchorCurr, (prev, curr) => {
        const itemFocusRatio = focusRatioDiff(prev) + focusRatio
        const newScroll = curr.offset - (focusOffset - itemFocusRatio * curr.height)
        return newScroll
    })
    return ensure(newScroll, scroll)
}
